import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import PokemonService from '../services/pokemon-service';

const PokemonSearch = () => {

  const [term, setTerm] = useState('');
  const [pokemons, setPokemons] = useState([]);

  const handleInputChange = (e) => {
    const term = e.target.value;
    setTerm(term);

    if(term.length <= 1) {
      setPokemons([]);
      return;
    }

    PokemonService.searchPokemon(term).then(pokemons => setPokemons(pokemons));
  }

  return (
    <div className="row">
    <div className="col s12 m6 offset-m3">
      <div className="card">
      <div className="card-content">
        <div className="input-field">
        <input type="text" placeholder="Rechercher un pok√©mon" value={term} onChange={e => handleInputChange(e)} />
        </div>
        <div className='collection'>
        {pokemons.map((pokemon) => (
          <Link key={pokemon.id} to={`/pokemons/${pokemon.id}`} className="collection-item">
            {pokemon.name}
          </Link>
        ))}
        </div>
      </div>
      </div>
    </div>
    </div>
  );
}

export default PokemonSearch;